.main-footer {
    text-align: center;
    @media (min-width: $tablet__width) {
      display: flex;
      padding: 36px 45px;
      align-items: center;
      }
  }
  
  .main-footer__title {
    font-family: "PT Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    /* identical to box height, or 124% */
    text-align: center;
    text-transform: uppercase;
  
    color: #000000;
    margin-bottom: 34px;
    @media (min-width: $tablet__width) { 
      margin: 0;
    }
  }
  
  .main-footer__text {
    font-family: "PT Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    /* identical to box height, or 150% */
    text-align: center;
    text-transform: uppercase;
  
    color: #000000;
  }
  
  .main-footer__item {
    background-color: #81b3d2;
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover path{
      fill:$hover__color;
      transition: 0.5s;
    }
    & path {
      transition: 0.5s;
    }
  }
  
  .main-footer__list {
    display: flex;
    margin: 0 auto;
    width: fit-content;
    gap: 6px;
    margin-bottom: 21px;
    @media (min-width: $tablet__width) { 
      margin-top: 36px; 
      margin: 0 auto;
    }
    @media (min-width: $desktop__width) {
      margin: 0 auto;
  }
}

  
  .main-footer__author {
    display: flex;
    justify-content: center;
    gap: 7px;
    align-items: center;
    @media (min-width: $desktop__width) {
      margin: 0; 
    }
  }